* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: start;
}

.country-item img {
margin-right: 10px;
}

.country-info {
  font-size: 20px;
  padding: 20px;
}
.country-info-box {
  display: flex;
  align-items: center;
}
.country-info-box img {
  margin-right: 10px;
}

.info-name {
  font-weight: bold;
}